import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import useConsumeChopRule from '../../../hooks/useConsumeChopRule';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import { Edit as EditIcon, Trash } from 'react-feather';
import consumeChopRuleService from '../../../services/consumeChopRuleService';
import DeleteConfirmCheck from '../../../components/DeleteConfirmCheck';
import { useTranslation } from 'react-i18next';
import Header from './Header';
// import Results from './Results';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function ConsumeChopRuleList() {
  const { t } = useTranslation();
  const { consumeChopRules, deleteConsumeChopRule } = useConsumeChopRule();
  // const [consumeChopRules, setConsumeChopRules] = React.useState([]);

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [needDeleteConsumeChopRule, setDeleteConsumeChopRule] = React.useState({});

  const handleClickOpen = (consumeChopRule) => {
    setOpen(true);
    setDeleteConsumeChopRule(consumeChopRule);
  };

  const handleDeleteConsumeChopRule = async () => {
    await deleteConsumeChopRule(needDeleteConsumeChopRule.id);
    setOpen(false);
    setDeleteConsumeChopRule({});
  };

  const handleClose = () => {
    setOpen(false);
  };

  const columns = [
    {
      title: t('Name'),
      field: 'name'
    },
    {
      title: t('Type'),
      field: 'type',
      render: (rowData) => t(`Type.${rowData.type}`)
    },
    {
      title: t('Actions'),
      field: 'actions',
      render: (rowData) => (
        <>
          <IconButton component={RouterLink} to={`/consumeChopRules/${rowData.id}/edit`}>
            <SvgIcon fontSize="small">
              <EditIcon />
            </SvgIcon>
          </IconButton>
          <IconButton onClick={() => handleClickOpen(rowData)}>
            <SvgIcon fontSize="small">
              <Trash />
            </SvgIcon>
          </IconButton>
        </>
      )
    }
  ];

  if (!consumeChopRules) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Consume Chop Rule List')}>
      <Container maxWidth={false}>
        <Header />
        {consumeChopRules && (
          <Box mt={3}>
            <MaterialTable
              title={t('Consume Chop Rule List')}
              columns={columns}
              data={consumeChopRules}
            />
          </Box>
        )}
      </Container>
      <DeleteConfirmCheck
        open={open}
        handleClose={handleClose}
        handleDelete={handleDeleteConsumeChopRule}
        checkText={needDeleteConsumeChopRule.name}
      />
    </Page>
  );
}
