import { useState, useEffect, useCallback } from 'react';
import useIsMountedRef from './useIsMountedRef';
import consumeChopRuleService from '../services/consumeChopRuleService';

export default function useConsumeChopRule() {
  const [consumeChopRules, setConsumeChopRules] = useState([]);
  const isMountedRef = useIsMountedRef();

  const getConsumeChopRules = useCallback(async () => {
    const data = await consumeChopRuleService.list();
    setConsumeChopRules(data.data.data);
  }, [isMountedRef]);

  const deleteConsumeChopRule = useCallback(
    async (id) => {
      await consumeChopRuleService.delete(id);
      getConsumeChopRules();
    },
    [getConsumeChopRules]
  );

  useEffect(() => {
    getConsumeChopRules();
  }, [getConsumeChopRules]);

  return { consumeChopRules, deleteConsumeChopRule };
}
